.frameChild {
  width: 1240px;
  height: 472px;
  position: relative;
  border-radius: var(--br-5xs);
  background-color: var(--purple-3);
  display: none;
  max-width: 100%;
}

.quoteIcon,
.testimonials1 {
  position: relative;
  z-index: 1;
}
.quoteIcon {
  height: 40px;
  width: 40px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: contain;
}
.testimonials1 {
  margin: 0;
  height: 36px;
  font-size: inherit;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
}
.frameWrapper,
.quoteParent {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.quoteParent {
  align-items: center;
  gap: var(--gap-5xl);
}
.frameWrapper {
  align-self: stretch;
  align-items: flex-start;
}
@media screen and (max-width: 750px) {
  .inAFast {
    font-size: 14px; /* Adjust the font size */
    line-height: 1.4; /* Adjust the line height */
  }
}

.audioTrackChild,
.frameItem {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.audioTrackChild {
  border-radius: var(--br-5xs);
  background-color: var(--purple-2);
  display: none;
}
.frameItem {
  border-radius: 50%;
  background-color: var(--grey-white);
  z-index: 1;
}
.playArrowBlack24dp1Icon {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 24px;
  height: 24px;
  overflow: hidden;
  z-index: 2;
}
.ellipseParent {
  position: absolute;
  top: 30px;
  left: 32px;
  width: 40px;
  height: 40px;
}
.audioTrackInner,
.audioTrackItem {
  position: absolute;
  border-radius: 50%;
}
.audioTrackItem {
  top: 44px;
  left: 112px;
  background-color: var(--primary-full);
  width: 12px;
  height: 12px;
  z-index: 2;
}
.audioTrackInner {
  top: 19px;
  left: 684px;
  width: 60px;
  height: 60px;
  object-fit: cover;
  z-index: 1;
}
.micBlack24dp1Icon {
  position: absolute;
  top: 47px;
  left: 672px;
  width: 32px;
  height: 32px;
  overflow: hidden;
  z-index: 2;
}
.div,
.frameInner {
  position: relative;
  z-index: 1;
}
.frameInner {
  height: 2px;
  flex: 1;
  border-radius: var(--br-11xs);
  background-color: var(--grey-white);
  min-width: 304px;
  max-width: 100%;
}
.div {
  line-height: 16px;
}
.rectangleGroup {
  position: absolute;
  top: 42px;
  left: 118px;
  width: 503px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
  max-width: 100%;
}
.audioTrack,
.ellipseIcon {
  height: 100px;
  position: relative;
  z-index: 1;
}
.audioTrack {
  flex: 1;
  border-radius: var(--br-5xs);
  background-color: var(--purple-2);
  min-width: 504px;
  max-width: 100%;
}
.ellipseIcon {
  width: 100px;
  border-radius: 50%;
  object-fit: cover;
}
.classicalDancer,
.shubhaNagarajan {
  position: relative;
  letter-spacing: 0.02em;
  z-index: 1;
}
.shubhaNagarajan {
  font-weight: 600;
}
.classicalDancer {
  height: 21px;
  font-size: var(--text-sm-bold-size);
  color: var(--blue-1);
  display: inline-block;
}
.ellipseGroup,
.shubhaNagarajanParent {
  display: flex;
  justify-content: flex-start;
}
@media screen and (max-width: 750px) {
  .audioTrackParent {
    flex-direction: column;
    gap: var(--gap-21xl);
  }

  .audioTrack {
    order: 2; /* Change the order to make it appear below */
    height: auto;
    min-height: 100%;
    min-width: 100%;
  }

  .ellipseGroup {
    order: 1; /* Change the order to make it appear above */
    flex-wrap: wrap;
  }
}

.shubhaNagarajanParent {
  flex-direction: column;
  align-items: flex-start;
  padding: var(--padding-xl) 0;
  gap: var(--gap-9xs);
}
.ellipseGroup {
  flex-direction: row;
  align-items: center;
  gap: var(--gap-5xl);
  min-width: 304px;
  font-size: var(--font-size-lg);
}
.audioTrackParent,
.inAFastGrowingAndEverChaParent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-61xl);
  max-width: 100%;
  font-size: var(--font-size-xs);
  color: var(--primary-full);
}
.inAFastGrowingAndEverChaParent {
  flex-direction: column;
  gap: var(--gap-21xl);
  font-size: var(--font-size-lg);
  color: var(--grey-darker);
}
.rectangleParent,
.testimonials {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangleParent {
  width: 1240px;
  border-radius: var(--br-5xs);
  background-color: var(--purple-3);
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-21xl);
  gap: var(--gap-13xl);
}
.testimonials {
  align-self: stretch;
  background-color: var(--grey-white);
  flex-direction: row;
  justify-content: center;
  padding: var(--padding-81xl) var(--padding-xl);
  z-index: 4;
  margin-top: -33px;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--grey-darkest);
  font-family: var(--text-sm-bold);
}

@media screen and (max-width: 1200px) {
  .audioTrackParent {
    gap: var(--gap-21xl);
  }
}
@media screen and (max-width: 1050px) {
  .ellipseGroup {
    flex: 1;
  }
  .audioTrackParent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .rectangleGroup {
    flex-wrap: wrap;
  }
  .audioTrack {
    height: auto;
    min-height: 100;
    min-width: 100%;
  }
  .audioTrackParent,
  .inAFastGrowingAndEverChaParent {
    gap: var(--gap-xl);
  }
  .rectangleParent,
  .testimonials {
    padding-top: var(--padding-81xl);
    box-sizing: border-box;
  }
  .rectangleParent {
    gap: var(--gap-base);
    padding-bottom: var(--padding-7xl);
  }
  .testimonials {
    padding-bottom: var(--padding-46xl);
  }
}
@media screen and (max-width: 450px) {
  .testimonials1 {
    font-size: var(--font-size-lgi);
  }
  .ellipseIcon {
    width: 20%;
  }
  .ellipseGroup {
    flex-wrap: wrap;
  }
}


